import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/components/Layout'
import Gobang from '@/components/Layout/gobang'
import Block from '@/components/Layout/block'
import pictures from '@/components/Layout/pictures'
import calendars from '@/components/Layout/calendars'
import forms from '@/components/Layout/forms'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Layout',
      component: Layout,
      children:[
        {
          name:'gobang',
          path:'gobang',
          component:Gobang,
        },
        {
          name:'block',
          path:'block',
          component:Block,
        },
        {
          name:'pictures',
          path:'pictures',
          component:pictures,
        },
        {
          name:'calendars',
          path:'calendars',
          component:calendars,
        },
        {
          name:'forms',
          path:'forms',
          component:forms,
        },
      ]
    }
  ]
})



