<template>
  <div id="container">
    <h1>{{title}}</h1>
    <div id="picLeft">
      <p>设置</p>
      <div class="configs">
        图片尺寸:<br>
        宽:<input type="text" style="width: 50px;margin-left: 5px" v-model="picWidth" @keyup="picWidths">&nbsp;(小于400)<br>
        高:<input type="text" style="width: 50px;margin-left: 5px" v-model="picHeight" @keyup="picHeights">&nbsp;(小于400)
      </div>
      <div class="configs">
        图片链接:
        <input type="text" v-model="picHref">
      </div>
      <div class="configs">
        图片描述:
        <input type="text" v-model="picTitle">
      </div>
      <div class="configs">
        图片显示特效:<br>
        <label for="zoomIn"><input type="radio" name="scale" id="zoomIn" value="zoomIn" v-model="scale">放大</label>
        <label for="zoomOut" style="margin-left: 10px;"><input type="radio" name="scale" id="zoomOut" value="zoomOut" v-model="scale">缩小</label>
      </div>
    </div>
    <div id="picRight">
      <a :href="'https://'+picHref" target="_blank"><img src="../../assets/logo.png" :class="scale" :title="picTitle" :style="{width:picWidth+'px',height:picHeight+'px'}"/></a>
    </div>
  </div>
</template>


<script>
  export default{
      name:'pictures',
      data(){
          return {
              title:'UI组件(图片)',
              picWidth:200,
              picHeight:200,
              picHref:'www.baidu.com',
              picTitle:'图片描述',
              scale:'zoomIn'
          }
      },
      methods:{
        picWidths(){
            if (this.picWidth>400) {
                this.picWidth=400;
            }else if (this.picWidth<0) {
                this.picWidth=200;
            }else if (this.picWidth>0 && this.picWidth<=400){
                return
            }else if (this.picWidth=='' || this.picWidth==0) {
                return
            }else {
                this.picWidth=200;
            }
        },
        picHeights(){
          if (this.picHeight>400) {
            this.picHeight=400;
          }else if (this.picHeight<0) {
            this.picHeight=200;
          }else if (this.picHeight>0 && this.picHeight<=400){
            return
          }else if (this.picHeight=='' || this.picHeight==0) {
            return
          }else {
            this.picHeight=200;
          }
        }
      },
      computed:{

      }
  }
</script>

<style scoped>
  #container {
    width: 700px;
    height: 700px;
  }
  h1 {
    text-align: center;
  }
  #picLeft {
    width: 200px;
    height: 350px;
    float: left;
    margin: 50px 20px 0 20px;
    overflow: hidden;
    border: 1px solid #eee;
  }
  #picLeft>p {
    width: 202px;
    line-height: 30px;
    padding-left: 10px;
    background-color: #42b19e;
    color: #fff;

  }
  #picRight {
    margin-top: 100px;
    width: 400px;
    height: 400px;
    float: left;
    text-align: center;
  }
  #picRight>img {
    margin-top: 50px;
  }
  #picRight .zoomIn,#picRight .zoomOut {
    transition: all .5s ease-in-out;
  }
  #picRight .zoomIn:hover {
    transform: scale(1.2,1.2);
  }
  #picRight .zoomOut:hover {
    transform: scale(.8,.8);
  }
  .configs {
    font-size: 13px;
    color: #999;
    padding-top: 5px;
    padding-left: 5px;
    line-height: 30px;
  }
</style>



