<template>
    <div id="container">
        <h1>{{title}}</h1>
        <template v-for="(inputs,index) in inputList">
          <label class="label">
            <span class="iName">{{inputs.titles}}</span>
            <input :type="inputs.type" v-model="inputs[dates[index]]" @blur="iBlur(index)" @focus="iFocus(index)" :class="{right:inputs.isPass==1,wrong:inputs.isPass==2}">
          </label>
          <p class="tip" :class="{right:inputs.isPass==1,wrong:inputs.isPass==2}">{{inputs.hint}}</p>
        </template>
        <input type="button" value="提交" id="btn" @click="submitMes">
        <table>
          <tr><th>账号</th><th>密码</th><th>邮箱</th><th>手机</th></tr>
          <tr v-for="user in userList"><td>{{user.fIdentity}}</td><td>{{user.fPassword}}</td><td>{{user.fEmail}}</td><td>{{user.fPhone}}</td></tr>
        </table>
    </div>
</template>

<script>
  export default{
      name:'forms',
      data(){
          return {
              title:'UI组件(表单)',
              dates:['fIdentity','fPassword','rePassword','fEmail','fPhone'],
              inputList:[
                {titles:'账号',type:'text',fIdentity:'',hint:'必填，长度为4-16个字符',isPass:0},
                {titles:'密码',type:'password',fPassword:'',hint:'必填，长度为4-16个字符',isPass:0},
                {titles:'确认密码',type:'password',rePassword:'',hint:'必填，再次输入密码',isPass:0},
                {titles:'邮箱',type:'text',fEmail:'',hint:'必填，xxx@xxx.xxx',isPass:0},
                {titles:'手机',type:'text',fPhone:'',hint:'必填，长度为11位数字',isPass:0},
              ],
              userList:[],
              tips:[
                {hint:'必填，长度为4-16个字符',right:'名称格式正确',wrong:'名称格式有误',regs:/^[a-zA-Z0-9_]{4,16}$/},
                {hint:'必填，长度为4-16个字符',right:'密码格式正确',wrong:'密码格式有误',regs:/^[a-zA-Z0-9_]{4,16}$/},
                {hint:'必填，再次输入密码',right:'两次密码输入一致',wrong:'两次密码输入不一致',regs:/^[a-zA-Z0-9_]{4,16}$/},
                {hint:'必填，xxx@xxx.xxx',right:'邮箱格式正确',wrong:'邮箱格式有误',regs:/^[a-zA-Z0-9]+@[a-zA-Z0-9]+\.[a-zA-Z]+$/},
                {hint:'必填，长度为11位数字',right:'手机格式正确',wrong:'手机格式有误',regs:/^[1][0-9]{10}$/}
              ],
          }
      },
      methods:{
          submitMes(){
              let check=0;
              for(let i=0;i<this.inputList.length;i++) {
                  if (this.inputList[i].isPass==1) {
                      check++;
                  }
              }
              if (check!=5) {
                  alert('提交失败！请检查输入的数据');
                  return
              }
              this.userList.push({
                fIdentity:this.inputList[0].fIdentity,
                fPassword:this.inputList[1].fPassword,
                fEmail:this.inputList[3].fEmail,
                fPhone:this.inputList[4].fPhone,
              });
          },
          iFocus(index){
              this.inputList[index].hint=this.tips[index].hint;
              this.inputList[index].isPass=0;
          },
          iBlur(index){
              if (index==2 || index==1) {
                  if (this.inputList[1][this.dates[1]]==this.inputList[2][this.dates[2]]) {
                      this.inputList[2].hint=this.tips[2].right;
                      this.inputList[2].isPass=1;
                  }else if (this.inputList[1][this.dates[1]]!=this.inputList[2][this.dates[2]]){
                      this.inputList[2].hint=this.tips[2].wrong;
                      this.inputList[2].isPass=2;
                  }
                  if (index==2) {return}
              }
              if (this.tips[index].regs.test(this.inputList[index][this.dates[index]])) {
                  this.inputList[index].hint=this.tips[index].right;
                  this.inputList[index].isPass=1;
              }else {
                  this.inputList[index].hint=this.tips[index].wrong;
                  this.inputList[index].isPass=2;
              }
          }
      }
  }
</script>

<style scoped>
  #container {
    width: 700px;
    min-height: 700px;
    text-align: center;
    overflow: auto;
  }
  h1 {
    text-align: center;
  }
  .label {
    display: inline-block;
    font-weight: bold;
  }
  .label:first-of-type {
    margin-top: 20px;
  }
  .label>input {
    margin-left: 20px;
    margin-right: 20px;
    width: 400px;
    height: 40px;
    border-radius: 10px;
    outline: none;
    border: 2px solid #999;
    padding-left: 20px;
  }
  .tip {
    margin: 10px 0 10px 180px;
    text-align: left;
  }
  .iName {
    width: 70px;
    display: inline-block;
  }
  #btn {
    height: 40px;
    width: 80px;
    margin-bottom: 10px;
  }
  #container table {
    border-collapse: collapse;
    margin: 0 auto;
  }
  #container th,td {
    border: 1px solid #000;
    height: 30px;
    line-height: 30px;
    width: 120px;
  }
  #container .right {
    border-color: #6BC058;
    color: #6BC058;
  }
  #container .wrong {
    border-color: red;
    color: red;
  }
</style>



