<template>
  <div id="container">
    <h1>{{title}}</h1>
    <div id="main">
      <input type="text" readonly="readonly" @click="isShow" v-model="showDate" style="text-align: center;">
      <div id="calendar" v-show="calendarShow">
          <p class="calTitle" v-if="inputRadio">
              <span>{{today.year+'年'+today.month+'月'}}</span>
              <span id="prevYear" @click="prevYear"><--</span>
              <span id="prevMonth" @click="prevMonth"><-</span>
              <span id="nextYear" @click="nextYear">--></span>
              <span id="nextMonth" @click="nextMonth">-></span>
          </p>
          <p class="calTitle" v-else>
            <select v-model="today.year" @change="calChange">
              <option v-for="n in 40" :value="n+1990">{{(n+1990)+'年'}}</option>
            </select>
            <select v-model="today.month"  @change="calChange" style="margin-left: 20px;">
              <option v-for="n in 12" :value="n">{{n+'月'}}</option>
            </select>
          </p>
          <div id="weeks">
            <span v-for="(day,index) in days" class="date" :class="{weekend:index==0||index==6}">{{day}}</span>
          </div>
          <div id="dates">
            <span v-for="n in renderData" class="date" @click="calClick(n)" :class="{weekend:(n-1)%7==0||(n-1)%7==6,unCurMonth:n.month!=today.month,todayStyle:n.todays}">{{n.day}}</span>
          </div>
      </div>
    </div>
    <div id="radio">
      <label for="inpBtn"><input type="radio" id="inpBtn" name="radio" value='true' v-model="inputRadio" @click="initDate">按钮式</label>
      <label for="inpSel"><input type="radio" id="inpSel" name="radio" value='' v-model="inputRadio" @click="initDate" style="margin-left: 10px;">下拉式</label>
    </div>
  </div>
</template>

<script>
  export default{
      name:'calendars',
      data(){
          return {
              title:'UI组件(日历)',
              calendarShow:true,
              days:['日','一','二','三','四','五','六'],
              today:null,
              curDate:null,
              showDate:null,
              renderData:[],
              inputRadio:true,
              todayTab:0,
              todayTabs:{},
          }
      },
      methods:{
          isShow(){
              this.calendarShow=!this.calendarShow;
          },
          init(){
              this.renderData=[];
              this.today={year:this.curDate.getFullYear(),month:this.curDate.getMonth()+1,day:this.curDate.getDate()};
              this.curDate.setDate(this.curDate.getDate()-this.curDate.getDay());
              for(let i=0;i<42;i++){
                  this.renderData.push({year:this.curDate.getFullYear(),month:this.curDate.getMonth()+1,day:this.curDate.getDate(),todays:false});
                  this.curDate.setDate(this.curDate.getDate()+1);
              }
          },
          prevYear(){
              this.curDate=new Date(this.today.year-1,this.today.month-1,1);
              this.init();
          },
          prevMonth(){
              this.curDate=new Date(this.today.year,this.today.month-2,1);
              this.init();
          },
          nextYear(){
              this.curDate=new Date(this.today.year+1,this.today.month-1,1);
              this.init();
          },
          nextMonth(){
              this.curDate=new Date(this.today.year,this.today.month,1);
              this.init();
          },
          calClick(n){
              this.renderData[this.todayTab].todays=false;
              n.todays=true;
              this.todayTab=this.renderData.indexOf(n);
              this.showDate=n.year+'年'+n.month+'月'+n.day+'日';
              if((n.month<this.today.month || n.year<this.today.year) && !(n.month==1 && this.today.month==12)){
                  this.todayTabs={month:n.month,day:n.day};
                  this.prevMonth();
                  for(let key in this.renderData){
                      if(this.todayTabs.month==this.renderData[key].month && this.todayTabs.day==this.renderData[key].day){
                          this.renderData[key].todays=true;
                          this.todayTab=this.renderData.indexOf(this.renderData[key]);
                      }
                  }
              }else if (n.month>this.today.month || n.month==1 && this.today.month==12) {
                  this.todayTabs={month:n.month,day:n.day};
                  this.nextMonth();
                  for(let key in this.renderData){
                      if(this.todayTabs.month==this.renderData[key].month && this.todayTabs.day==this.renderData[key].day){
                          this.renderData[key].todays=true;
                          this.todayTab=this.renderData.indexOf(this.renderData[key]);
                      }
                  }
              }

          },
          calChange(){
              this.curDate=new Date(this.today.year,this.today.month-1,1);
              this.today={year:this.curDate.getFullYear(),month:this.curDate.getMonth()+1,day:this.curDate.getDate()};
              this.init();
          },
          initDate(){
              this.curDate=new Date();
              this.today={year:this.curDate.getFullYear(),month:this.curDate.getMonth()+1,day:this.curDate.getDate()};
              this.curDate.setDate(1);
              this.init();
          }
      },
      created(){
          this.curDate=new Date();
          this.today={year:this.curDate.getFullYear(),month:this.curDate.getMonth()+1,day:this.curDate.getDate()};
          this.curDate.setDate(1);
          this.init();
      }
  }
</script>

<style scoped>
  #container {
    width: 700px;
    height: 700px;
    text-align: center;
  }
  h1 {
    text-align: center;
  }
  #main {
    margin-top: 20px;
    height: 430px;
  }
  #calendar {
    border: 1px solid #999;
    width: 350px;
    height: 400px;
    margin: 0 auto;
  }
  .calTitle {
    text-align: center;
    height: 30px;
    line-height: 30px;
    background-color: #c81b01;
    color: #fff;
    font-weight: bold;
  }
  #prevYear,#prevMonth {
    float: left;
    margin-left: 10px;
    height: 30px;
    width: 30px;
    cursor: pointer;
  }
  #nextMonth,#nextYear {
    float: right;
    margin-right: 10px;
    height: 30px;
    width: 30px;
    cursor: pointer;
  }
  .date {
    display: inline-block;
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    box-sizing: border-box;
    user-select: none;
  }
  .todayStyle {
    background-color: rgb(204, 155, 41);
    color: #fff;
  }
  .unCurMonth {
    color: lightgray;
  }
  #dates .date {
    cursor: pointer;
  }
  #dates .date:hover {
    background-color: skyblue;
    color: #fff;
  }
  .weekend {
    color: #c81b01;
  }
  #radio {
    margin-top: 30px;
  }
</style>



